:root{
    --primary:#fff
}
.btn{
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}
.btn--primary {
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
}
.btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: sll 0.3s ease-out;
}
.btn--medium{
    padding: 8px 20px;
    font-size: 20px;

}
.btn--large {
    padding: 12px 26px;
    font-size: 20px;

}
.btn--large:hover ,.btn--medium:hover{
    background: #fff;
    color: #242424;
    transition: sll 0.3s ease-out;
}
.btn-mobile{
    background-color: transparent;
        color: #fff;
        padding: 8px 20px;
        border: 1px solid var(--primary);
        transition: sll 0.3s ease-out;
}