.navbar {
    background: linear-gradient(90deg,rgb(28,27,27) 0%,rgb(26,27,27) 100%);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1,2rem;
    position: sticky;
    top :0;
    z-index: 999;

}
.navbar-container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    max-width : 1500px;

}
.navbar-container{
    color: aliceblue;
    justify-content: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;

}
.navbar-logo{
    text-decoration: none;
    color: #fff;
    margin-left: 0px;
}


.nav-menu{
    display: flex;
    grid-template-columns: repeat(4,auto);
    grid-gap: 30px;
    list-style: none;
    text-align: center;
    width: 60vw;
    justify-content: end;
    margin-right: 2rem;

}
.nav-item{
    height: 80px; 
}
.nav-links{
    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
}
.nav-links:hover{
    border-bottom: 4px solid#fff;
    transition: all 0.2s ease-out;
}
.fa-bars{
    color: #fff;
}
.nav-links-mobile{
    display: none;
}
