video{
   object-fit: cover;
   width: 100%;
   height: 100%;
   position: fixed;
   z-index: -1;  
}
.hero-container {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgb(0, 0
    ,0.2);
    object-fit: contain ;
}
.hero-container > h1{
    color: #fff;
    font-size: 100px;
    margin-top: -100px;

}
.hero-container > p {
    margin-top: 8px;
    color: #fff;
    font-size: 32px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    
}